import rules from './rules';
import { isTable, isHeadingRow } from './utils';

function tables(turndownService: any) {
  turndownService.keep(function (node: HTMLElement) {
    return isTable(node) && !isHeadingRow(node.rows[0]);
  });
  for (const key in rules) {
    turndownService.addRule(key, rules[key]);
  };
}

function gfm(turndownService: any) {
  turndownService.use([tables]);
}

export default {
  name: 'table',
  use: gfm,
};
